export const generate_theme_colours = (value: any) => {
  // color 1 to HSL
  let m1 = {}
  m1 = hexToHSL(value)

  // calculating color 2
  let m2h_temp = 0
  m2h_temp = m1.h - 12
  if (m2h_temp > 360) {
    m2h_temp = m2h_temp - 360
  } else if (m2h_temp < 0) {
    m2h_temp = m2h_temp + 360
  }

  // calculating color 3
  let m3h_temp = 0
  m3h_temp = m1.h - 170
  if (m3h_temp > 360) {
    m3h_temp = m3h_temp - 360
  } else if (m3h_temp < 0) {
    m3h_temp = m3h_temp + 360
  }

  let m2 = ('')
  m2 = HSLToHex(m2h_temp, 50, 45)

  let m3 = ('')
  m3 = HSLToHex(m3h_temp, 93, 54)

  let s1 = ('')
  s1 = HSLToHex(m1.h, 30, 70)

  let s2 = ('')
  s2 = HSLToHex(m2h_temp, 30, 70)

  let s3 = ('')
  s3 = HSLToHex(m3h_temp, 30, 70)

  let t1 = ('')
  t1 = HSLToHex(m1.h, 50, 95)

  let t2 = ('')
  t2 = HSLToHex(m2h_temp, 50, 95)

  let t3 = ('')
  t3 = HSLToHex(m3h_temp, 50, 95)
  // console.log(m2_temp, m1.s, 29);

  let data = {
    color_11: '#ad43f5',
    color_12: '#78ce6a',
    color_13: '#EC7A4A',
    color_14: '#f5d343',
    color_15: '#76c2f6',
    color_red: '#e00000',
    color_green: '#639A4D',
    color_m1: value,
    color_m2: m2,
    color_m3: m3,
    color_s1: s1,
    color_s2: s2,
    color_s3: s3,
    color_t1: t1,
    color_t2: t2,
    color_t3: t3,
  }

  // console.log(data)
  return data
}

